.b-logo {
	display: flex;
	flex-direction: column;
	align-items: flex-end;
	position: relative;
	z-index: 6;
	&__img {
		svg {
			width: 171px;
			height: 35px; } }

	&__title {
		display: none;
		color: $color-font-light; }


	//.b-logo
	@media screen and (min-width: $md) {
		&__img {
			max-width: 151px; } }
	@media screen and (min-width: $lg) {
		&__img {
			max-width: 100%;
			svg {
				width: 173px;
				height: 36px;
 }				//margin-bottom: 4px
			&--footer {
				width: 110px;
				height: 35px;
				margin-bottom: 0; } }
		&__title {
			font-size: 10px;
			display: block;
 } }			//margin-left: 66px

	@media screen and (min-width: $xl) {
		&__img {

			svg {
				width: 258px;
				height: 53px; }
			&--footer {
				width: 110px;
				height: 35px;
				margin-bottom: 0; } }

		&__title {
			margin-top: 6px;
			font-size: 14px;
 } } }			//margin-left: 74px
