.b-product {
	padding: 15px 10px;
	background-color: #ffffff;
	box-shadow: 0 5px 10px rgba(50, 60, 90, 0.04);
	margin-bottom: 30px;
	&__caption {
		font: 20px/1.3 $font-addition;
		margin-bottom: 13px; }

	&__price {
		margin-bottom: 24px; }

	&__button-wrap {
		text-align: right; }

	&__content {
		border-bottom: 1px solid #9da6bf;
		padding-bottom: 12px; }


	@media screen and (min-width: $md) {
		padding-left: 5px;
		padding-right: 5px;
		display: flex;
		&__content {
			flex: 1 0 50%;
			max-width: 50%;
			order: 1;
			border-bottom: none;
			padding-bottom: 0;

			border-left: 1px solid #9da6bf;
			padding-left: 29px;
			padding-top: 12px; }
		&__caption {
			margin-bottom: 18px; }

		&__img {
			order: 0;
			flex: 1 0 50%;
			max-width: 50%; }
		&__button-wrap {
			text-align: left; }
		&__price {
			margin-bottom: 29px; } }

	@media screen and (min-width: $lg) {
		padding-top: 30px;
		padding-bottom: 30px;
		margin-bottom: 58px;
		&__content {
			padding-top: 60px; }
		&__caption {
			font-size: 28px;
			margin-bottom: 45px; }
		&__price {
			margin-bottom: 56px; } } }

.b-product-price {

	&__row {
		display: flex;
		align-items: center;
		margin-bottom: 9px; }
	&__label {
		font-weight: 300;
		line-height: 1;
		flex: 0 0 29%;
		max-width: 29%; }
	&__value {
		font-size: 16px;
		color: $color-silver;
		&--old {
			text-decoration: line-through; } }

	&__new-price {
		font-size: 28px;
		font-weight: 900;
		text-transform: uppercase;
		color: $addition-color; }

	@media screen and (min-width: $md) {
		&__label {
			flex: 0 0 27%;
			max-width: 27%; } }

	@media screen and (min-width: $lg) {
		&__label {
			font-size: 16px;
			flex: 0 0 18%;
			max-width: 18%; }
		&__value {
			font-size: 20px; }
		&__new-price {
			font-size: 38px; } } }
