.b-breadcrumb {
	//display: flex
	//align-items: center
	//flex-wrap: wrap
	margin-bottom: 30px;

	&__item {
		position: relative;
		display: inline;
		&:after {
			content: '/';
			display: inline-block;
			margin: 0 3px; }

		&:last-child {
			&:after {
				display: none; } } }
	&__link {
		text-decoration: underline; }
	&__link,
	&__caption {
		font-size: 14px; }

	&__caption {
		color: $color-font; }
	@media screen and (min-width: $lg) {
		margin-bottom: 60px;
		&--gutter-bottom-30 {
			margin-bottom: 30px; } } }
