.b-search-options {
	margin: 10px 0;
	display: flex;
	align-items: center;
	justify-content: space-between;
	flex-wrap: wrap;
	&--table {
		display: none; }
	&__caption {
		font-size: 20px;
		color: #ffffff;
		padding-right: 7px; }
	&__item {
		padding-right: 7px; }
	//.b-search-options
	@media (min-width: $md) and (max-width: $max-lg) {
		&--tablet-hidden {
			display: none; } }
	@media screen and (max-width: $max-md) {
		&__caption {
			width: 100%;
			margin-bottom: 25px; }
		&__btn {
			border-color: $dark;
			color: $dark; } }
	@media screen and (max-width: $max-sm) {
		flex-direction: column;
		&__caption {
			display: none; }

		&__item {
			margin: 3px 0;
			width: 100%; }

		&__btn {
			border-color: #ffffff;
			color: #ffffff;
			margin-top: 10px; } }

	@media screen and (min-width: $sm) {
		&--table {
			display: flex; } }
	@media screen and (min-width: $md) {
		&__caption {
			color: #49505a; } }
	@media screen and (min-width: $lg) {

		&--table {
			display: none; }

		&__caption {
			font-size: 28px;

			flex: 0 0 28%;
			max-width: 28%; }

		&__item {
			padding-right: 15px;

			flex: 0 0 19%;
			max-width: 19%; }

		&__btn {
			margin-left: auto; } }

	@media screen and (min-width: $xl) {
		padding-left: 2.4%;
		padding-right: 2.4%; } }
