.b-picked-coupons {
	//display: none

	&--mobile {
		//display: block
		padding-left: calc(4% + 10px);
		padding-right: calc(4% + 10px);
		margin-bottom: 30px; }

	&--desctop,
	&--tablet {
		display: none; }
	&--row {
		display: flex;
		align-items: center;
		justify-content: space-between; }
	&__caption {
		&--white {
			color: #ffffff;
			font-weight: 400; }
		&--gutter-0 {
			margin: 0; } }
	&__desc {
 }		//max-width: 29.5%
	&__desc-text {
		color: $color-font;
		margin-bottom: 30px;
		&--white {
			color: $accent-light; } }
	&__slider {
		.tns-inner {
			margin: 0 !important; } }


	//.b-picked-coupons

	@media (max-width: $max-lg) and (min-width: $md) {
		&--on-list {
			padding-left: 8%; }
		&--tablet {
			padding-left: calc(3.25% + 10px);
			display: block; }

		&__slider {
			&--style-2 {
				.b-slider-controls__nav.__next {
					right: 20px; } } } }

	@media screen and (max-width: $max-md) {
		&__desc {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between; }

		&__desc-text {
			order: 1;
			flex: 1 0 100%;
			max-width: 100%; }
		&__caption {
			margin-bottom: 10px; } }


	@media screen and (min-width: $sm) {
		&__slider {
			.tns-inner {
				margin: 0 40px 0 0 !important; }
			&--style-2 {
				.tns-inner {
					margin: 0 160px 0 0 !important; } } }
		&--mobile {
			display: none; } }
	@media screen and (min-width: $md) {
		&__caption {
			&--white-table {
				color: #ffffff; } }
		&__text {
			&--white-table {
				color: $accent-light; } }
		&__desc {
			padding-right: 4%;
			max-width: 29.5%; }
		&__slider {
			max-width: 70.5%; } }

	@media screen and (min-width: $lg) {
		padding-left: calc(4.25% + 10px);
		&--desctop {
			display: block; }
		&__slider {
			.b-slider-controls__nav.__next {
				right: 20px; } } } }

