.b-explanation {

	background-color: #eceef3;
	padding: 15px;

	&__list {
		padding-left: 14px;
		margin: 15px 0 0px; }

	&__list-item {
		line-height: 1.3; }


	&__footer {
		margin-top: 15px; }

	@media (max-width: $max-lg) and (min-width: $sm) {
		&__picture {
			flex: 0 0 58.33333%;
			max-width: 58.33333%; }
		&__list-wrap {
			flex: 0 0 41.66667%;
			max-width: 41.66667%; } }



	@media screen and (min-width: 670px) {
		&__list {
			margin: 39px 0 0px; } }
	@media screen and (min-width: $sm) {
		&__footer {
			margin-top: 30px; } }
	@media screen and (min-width: $lg) {
		&__list-item {
			font-size: 16px; } }
	@media screen and (min-width: $xl) {

		&__picture {
			flex: 0 0 58.33333%;
			max-width: 58.33333%; }
		&__list-wrap {
			flex: 0 0 41.66667%;
			max-width: 41.66667%; } } }
