.e-button {
  position: relative;
  z-index: 1;
  display: inline-block;
  font: 400 12px/1.2 "Roboto", Helvetica, Arial, sans-serif;
  color: #ffffff;
  padding: 5px 16px 7px;
  border: 2px solid transparent;
  text-align: center;
  border-radius: 1.8em;
  -webkit-border-radius: 1.8em;
  -moz-border-radius: 1.8em;
}

.e-button--transp {
  border-color: #ffffff;
}

.e-button--dark {
  color: #323c5a;
  border-color: #323c5a;
}

.e-button--add-accent {
  border-color: transparent;
  background-color: #36c6fe;
}

.e-button--xl {
  font-size: 14px;
  padding: 13px 23px 13px;
}

.e-button--arrow-right {
  padding-right: 45px;
}

.e-button--arrow-right:after {
  content: '';
  position: absolute;
  top: 50%;
  right: 19px;
  -webkit-transform: translate(0, -50%);
  -moz-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  transform: translate(0, -50%);
  display: block;
  width: 13px;
  height: 13px;
  background: transparent url(data:image/svg+xml;base64,) center no-repeat;
  background-size: contain;
}

.e-button:before {
  content: '';
  position: absolute;
  top: -2px;
  right: -2px;
  bottom: -2px;
  left: 50%;
  background-color: #b34eb0;
  z-index: -1;
  width: 0;
  border-radius: 1.8em;
  -webkit-border-radius: 1.8em;
  -moz-border-radius: 1.8em;
  -webkit-transition: all 0.4s ease;
  -moz-transition: all 0.4s ease;
  -ms-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease;
}

.e-button:hover, .e-button:focus {
  color: #ffffff;
  border-color: transparent;
}

.e-button:hover:before, .e-button:focus:before {
  left: -2px;
  width: calc(100% + 4px);
}

