.e-button {
	position: relative;
	z-index: 1;
	display: inline-block;
	font: 400 12px/1.2 $font-defoult;

	color: #ffffff;
	padding: 5px 16px 7px;
	border: 2px solid transparent;

	text-align: center;

	@include border-radius(1.8em);

	&--transp {
		border-color: #ffffff; }
	&--dark {
		color: $dark;
		border-color: $dark; }
	&--add-accent {
		border-color: transparent;
		background-color: $addition-color; }

	&--xl {
		font-size: 14px;
		padding: 13px 23px 13px; }
	&--arrow-right {
		padding-right: 45px;
		&:after {
			content: '';
			position: absolute;
			top: 50%;
			right: 19px;
			@include translate(0, -50%);
			display: block;
			width: 13px;
			height: 13px;

			background: transparent url(data:image/svg+xml;base64,) center no-repeat;
			background-size: contain; } }
	&:before {
		content: '';
		position: absolute;
		top: -2px;
		right: -2px;
		bottom: -2px;
		left: 50%;
		background-color: $accent;
		z-index: -1;
		width: 0;
		@include border-radius(1.8em);
		@include transition(all, .4s, ease); }

	&:hover,
	&:focus {
		color: #ffffff;
		border-color: transparent;
		&:before {
			left: -2px;
			width: calc(100% + 4px); } }




	//.e-button
	@media screen and (max-width: $max-md) {
		&--mobile-dark {
			color: $dark;
			border-color: $dark; } }

	@media screen and (min-width: $md) {
		&--xl {
			padding: 13px 19px 13px; }

		&--arrow-right {
			padding-right: 49px; } }

	@media screen and (min-width: $lg) {
		font-size: 16px;
		padding: 5px 26px 7px;
		min-width: 120px;
		&--xl {
			font-size: 20px;
			padding: 18px 27px 20px; }
		&--arrow-right {
			padding-right: 60px;
			&:after {
				right: 27px;
				width: 15px;
				height: 15px; } } }
	@media screen and (min-width: $xl) {
		min-width: 143px; } }

.b-buton {

	&--right {
		text-align: right; } }
