.b-caption {
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin-bottom: 20px;
	&--gutter-60 {
		margin-bottom: 30px; }

	&--border-bottom {
		border-bottom: 3px solid $dark;
		padding-bottom: 12px; }

	&__title {
		font-weight: 400;
		font-size: 20px;
		margin-bottom: 0; }
	&__line {
		margin: 0 5%;
		display: block;
		height: 1px;
		background-color: #a9b1c7;
		flex: 1; }
	.e-button {
		font-size: 14px; }
	//.b-caption
	@media screen and (min-width: $lg) {
		margin-bottom: 35px;
		&--gutter-60 {
			margin-bottom: 60px; }
		&__title {
			font-size: 28px; }
		&__line {
			margin: 0 3%; } } }
