.b-border-left {
	margin: 50px 0;
	&__img {
		//margin: 63px auto 63px 10.5%
		//max-width: 680px
		margin: 25px auto; }
	@media screen and (min-width: $md) {
		margin: 50px auto 50px 10%;
		padding: 24px 0 24px  4.7%;
		border-left: 3px solid #323c5a; }

	@media screen and (min-width: $lg) {

		margin: 60px auto 60px 10%;
		padding: 30px 0 30px  4.7%;
		&__img {
			max-width: 680px;
			margin: 63px auto 63px 10.5%; } } }
