.b-product-slider {

	margin-bottom: 36px;
	&--gutter-0 {
		margin-bottom: 0 !important; }
	@media screen and (min-width: $md) {
		margin-bottom: 30px;
		&__container {
			width: 96%;
			margin: 0 auto;
			padding-left: 10px;
			padding-right: 10px; }

		.b-slider-controls__nav {
			&.__prev {
				left: -15px; }
			&.__next {
				right: -15px; } } }

	@media screen and (min-width: $lg) {
		margin-bottom: 66px; } }
