.b-select {
	display: inline-block;
	position: relative;
	width: 100%;
	&:before {
		content: '';
		position: absolute;
		top: 50%;
		right: 30px;
		display: block;

		width: 17px;
		height: 7px;
		background: transparent url(../assets/img/arrow-bottom.svg) no-repeat center;
		background-size: cover;

		@include translate(0, -50%); }

	&__select {
		font-size: 14px;
		color: #49505a;
		min-width: 134px;
		min-height: 37px;
		@include border-radius(30px);
		border: 2px solid #e0e3eb;
		background: #ffffff;
		-webkit-appearance: none;
		-moz-appearance: none;
		appearance: none;

		padding: 9px 40px 9px 30px;

		width: 100%; }
	//.b-select
	@media screen and (min-width: $sm) {
		&:before {
			right: 20px; }
		&__select {
			padding: 9px 40px 9px 15px; } }
	@media screen and (min-width: $lg) {
		//width: 100%
		&__select {
			//width: 100%
			font-size: 16px;
			padding: 13px 55px 13px 30px; } } }
