.b-articles-list {
	padding-bottom: 60px;
	&--table-only {
		display: none; }
	&--category {
		padding-top: 35px;
		padding-bottom: 35px;
		background-image: linear-gradient(-36deg, #eef1f6 0%, #eff2f7 9%, #eff2f7 9%, #f3f5f9 38%, #f5f7f9 49%, #f6f7fa 54%, #fcfdfd 100%); }

	&__item {
		flex: 0 0 100%;
		max-width: 100%;

		padding: 10px 30px; }

	//.b-articles-list
	@media (min-width: $sm) and (max-width: $max-lg) {
		&--table-hidden {
			display: none; }
		&--table-only {
			display: block; } }
	@media screen and (min-width: $md) {
		&__item {
			flex: 0 0 50%;
			max-width: 50%; } }
	@media screen and (min-width: $lg) {
		&--category {
			padding-top: 65px;
			padding-bottom: 65px; }
		&--table-only {
			display: none; }
		&__item {
			flex: 0 0 33.333%;
			max-width: 33.333%; } } }
